import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import './LoginPage.css';

const LoginPage = () => {
  const navigate = useNavigate();
  const [userType, setUserType] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');

  //     
  const isValidEmail = (email) => {
    return /^\S+@\S+\.\S+$/.test(email);
  };

  //     
  const isValidPassword = (password) => {
    return /^(?=.*[A-Za-z])(?=.*[\d!@#$%^&*()_+]).{8,}$/.test(password);
  };

  const handleLogin = () => {
    if (!userType) {
      alert('  .');
      return;
    }
    if (!email) {
      alert(' .');
      return;
    }
    if (!isValidEmail(email)) {
      alert('   .');
      return;
    }
    if (!password) {
      alert(' .');
      return;
    }
    if (!isValidPassword(password)) {
      alert(
        '  , /  2  8  .'
      );
      return;
    }

    console.log(` : ${userType}, ${email}`);
  };

  return (
    <div className="login-container">
      <p className="description">
        !
        <br /> , , ,      {' '}
        <strong>Logismate</strong>.
      </p>
      <h2 className="login-title"></h2>

      <div className="user-type">
        <label>
          <input
            type="radio"
            name="userType"
            value="shipper"
            onChange={() => setUserType('shipper')}
          />
          
        </label>
        <label>
          <input
            type="radio"
            name="userType"
            value="forwarder"
            onChange={() => setUserType('forwarder')}
          />
          
        </label>
      </div>

      <input
        type="email"
        placeholder=""
        value={email}
        onChange={(e) => setEmail(e.target.value)}
      />
      <input
        type="password"
        placeholder=""
        value={password}
        onChange={(e) => setPassword(e.target.value)}
      />

      <button className="login-button" onClick={handleLogin}>
        
      </button>

      <p className="signup-prompt">
          ?
        <br />    .
      </p>

      <button
        className="signup-button"
        onClick={() => navigate('/signup-selection')}
      >
        
      </button>
    </div>
  );
};

export default LoginPage;
