import React from 'react';
import './styles/ForwarderTable.css';

const tableData = [
  { label: '/', value: '' },
  { label: '//', value: '' },
  { label: '-', value: ' -  (Aktau) ' },
  { label: ' ', value: 'CFR (Const and Freight)' },
  { label: '', value: '20FT   X 5' },
  { label: '  ', value: '2025.02.12 24:00' },
  { label: '', value: ' ,  ' },
  { label: '  ', value: '   ()' },
  { label: '', value: 'CIF (   ' },
  { label: ' ', value: '15,895,800 ~ 19,345,200' },
  { label: ' ', value: '   ' },
  { label: '   ', value: '2(2025.02.03 ~ 02.16)' },
  { label: ' ', value: '4/5' },
  { label: ' ', value: '   ' },
];

const ForwarderTable = () => {
  return (
    <table className="forwarder-table">
      <tbody>
        {tableData.map((row, index) => (
          <tr key={index}>
            <td className="table-label">{row.label}</td>
            <td className="table-value">{row.value}</td>
          </tr>
        ))}
      </tbody>
    </table>
  );
};

export default ForwarderTable;
