import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import '../../style/ShipperSignup.css'; //   

const ShipperSignup = () => {
  const navigate = useNavigate();
  const [formData, setFormData] = useState({
    email: '',
    password: '',
    companyName: '',
    businessNumber: '',
    phone: '',
    address: '',
  });

  const [errors, setErrors] = useState({});

  const validateForm = () => {
    const newErrors = {};

    if (!formData.email) {
      newErrors.email = ' .';
    } else if (!/^\S+@\S+\.\S+$/.test(formData.email)) {
      newErrors.email = '   .';
    }

    if (!formData.password) {
      newErrors.password = ' .';
    } else if (
      !/^(?=.*[A-Za-z])(?=.*[\d!@#$%^&*()_+]).{8,}$/.test(formData.password)
    ) {
      newErrors.password =
        ' , /  2  8  .';
    }

    if (!formData.companyName) {
      newErrors.companyName = ' .';
    }

    if (!formData.businessNumber) {
      newErrors.businessNumber = ' .';
    } else if (!/^\d{13}$/.test(formData.businessNumber)) {
      newErrors.businessNumber = ' 13  .';
    }

    if (!formData.phone) {
      newErrors.phone = ' .';
    } else if (!/^\d{3}-\d{4}-\d{4}$/.test(formData.phone)) {
      newErrors.phone = ' 000-0000-0000  .';
    }

    if (!formData.address) {
      newErrors.address = '  .';
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
    setErrors({ ...errors, [name]: '' });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (validateForm()) {
      navigate('/signup-complete', { state: { userType: '' } });
    } else {
      alert(' .');
    }
  };

  return (
    <div className="signup-container">
      <h2 className="signup-title"> ()</h2>
      <hr className="divider" />
      <form className="signup-form" onSubmit={handleSubmit}>
        <label></label>
        <input
          type="email"
          name="email"
          placeholder="logismate@gmail.com"
          value={formData.email}
          onChange={handleChange}
        />
        {errors.email && <p className="error">{errors.email}</p>}

        <label></label>
        <input
          type="password"
          name="password"
          placeholder=", ,   2  8 "
          value={formData.password}
          onChange={handleChange}
        />
        {errors.password && <p className="error">{errors.password}</p>}

        <label></label>
        <input
          type="text"
          name="companyName"
          placeholder=""
          value={formData.companyName}
          onChange={handleChange}
        />
        {errors.companyName && <p className="error">{errors.companyName}</p>}

        <label></label>
        <input
          type="text"
          name="businessNumber"
          placeholder="13 "
          value={formData.businessNumber}
          onChange={handleChange}
        />
        {errors.businessNumber && (
          <p className="error">{errors.businessNumber}</p>
        )}

        <label></label>
        <input
          type="text"
          name="phone"
          placeholder="010-1234-5678"
          value={formData.phone}
          onChange={handleChange}
        />
        {errors.phone && <p className="error">{errors.phone}</p>}

        <label></label>
        <input
          type="text"
          name="address"
          placeholder=" OO OO OO O"
          value={formData.address}
          onChange={handleChange}
        />
        {errors.address && <p className="error">{errors.address}</p>}

        <button type="submit" className="signup-button">
          
        </button>
      </form>
    </div>
  );
};

export default ShipperSignup;
