import React from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { FiX } from 'react-icons/fi'; //X  
import Navbar from '../Navbar';
import '../../style/ContractManagement.css';

const ContractManagement = () => {
  const location = useLocation();
  const navigate = useNavigate();
  const selectedDocument = location.state?.selectedDocument || ' ';

  // 
  const currentContracts = [
    {
      id: '049885',
      date: '2025-02-05 16:11',
      condition: ' ',
      transport: ' ',
      insurance: ' ',
    },
    {
      id: '049886',
      date: '2025-02-06 10:30',
      condition: ' ',
      transport: ' ',
      insurance: ' ',
    },
  ];

  //  
  const pastContracts = [
    { id: '039284', details: '  ', payment: ' ' },
  ];

  return (
    <div>
      <Navbar />
      <div className="contract-management-container">
        {/*X  */}
        <button
          className="close-icon"
          onClick={() => navigate('/forwarder-dashboard')}
        >
          <FiX size={30} />
        </button>

        <div className="title-container">
          <h2 className="title">  </h2>
          <p className="description">  !</p>
        </div>

        <div className="contract-table">
          <div className="table-header">
            <span> </span>
            <span></span>
            <span></span>
            <span>   </span>
          </div>
          {currentContracts.map((contract) => (
            <div key={contract.id} className="table-row">
              <span>{contract.date}</span>
              <span>{contract.condition}</span>
              <span>{contract.transport}</span>
              <span>{contract.insurance}</span>
            </div>
          ))}
        </div>

        <h3 className="subtitle">  </h3>
        <div className="past-contracts">
          <div className="table-header">
            <span></span>
            <span></span>
            <span> </span>
          </div>
          {pastContracts.map((contract) => (
            <div key={contract.id} className="table-row">
              <span>: {contract.id}</span>
              <span>{contract.details}</span>
              <span>{contract.payment}</span>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default ContractManagement;
