import React from 'react';

// Card 
export const Card = ({ children, className }) => {
  return (
    <div className={`bg-white shadow-md p-4 rounded-lg ${className}`}>
      {children}
    </div>
  );
};

// CardContent 
export const CardContent = ({ children }) => {
  return <div className="p-2">{children}</div>;
};

// Button 
export const Button = ({
  children,
  variant = 'default',
  className,
  ...props
}) => {
  const baseStyle =
    variant === 'outline' ? 'border border-gray-300' : 'bg-blue-500 text-white';
  return (
    <button className={`${baseStyle} p-2 rounded ${className}`} {...props}>
      {children}
    </button>
  );
};

// ForwarderCard  (  )
const ForwarderCard = ({ data }) => {
  return (
    <Card className="mb-4">
      <CardContent>
        <h2 className="text-xl font-bold">{data.name}</h2>
        <p>  : {data.estimatedCost} </p>
        <p> : {data.duration}</p>
      </CardContent>
    </Card>
  );
};

export default ForwarderCard;
